body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.col {
    height: 100%;
}

.logo {
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 20px;
    /*background: #eee;*/
}

.groups {
    height: 100%;
    border: solid 0.5px #ccc;
}

.groups > .content {
    height: calc(100% - 50px);
    overflow-y: auto;
}

.groups > .input {
    height: 50px;
    width: 100%;
    /*margin-bottom: 15px;*/
}

.messages {
    height: 100%;
    border: solid 0.5px #ccc;
    padding: 5px;
}

.messages > .content {
    height: calc(100% - 50px);
    overflow-y: auto;
}

.messages > .input {
    height: 50px;
}

.msg-user {
    font-size: 16px;
    padding-right: 10px;
}

.msg-content {
    color: #777;
}

.users {
    height: 100%;
    border: solid 0.5px #ccc;
}

.users > .content {
    height: calc(100% - 50px);
    overflow-y: auto;
}

.users > .input {
    height: 50px;
    width: 100%;
    /*margin-bottom: 15px;*/
}
